import { Component, OnInit, Input } from '@angular/core';
import {Card} from "../inventory-service.service";
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import {
  trigger,
  state,
  style,
  animate,
  transition,
  // ...
} from '@angular/animations';
@Component({
  selector: 'app-bscard',
  templateUrl: './bscard.component.html',
  styleUrls: ['../my-bestseller-app/my-bestseller-app.component.sass'],
  animations: [
    trigger('priceApear', [
      // ...
      state('noSelect', style({
        color: '#fff',
        padding: '0px'
      })),
      state('select', style({
        color: 'gray',
        padding: '5px'
      })),
      transition('noSelect => select', [
        animate('0.2s')
      ]),
      transition('select => noSelect', [
        animate('1s')
      ]),
    ]),
  ],
})
export class BscardComponent implements OnInit {
  @Input() card: Card;
  isActive:boolean = false;
  COP:string = 'COP'
  constructor() { }

  ngOnInit() {
  }

  mouseOver() {this.isActive = true;}

  mouseLeave() {this.isActive = false;}

}
