import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MyIndexAppComponent } from './my-index-app/my-index-app.component';
import { MyListProductsAppComponent } from './my-list-products-app/my-list-products-app.component';
import { MyProductAppComponent } from './my-product-app/my-product-app.component';
import { MyCarAppComponent } from './my-car-app/my-car-app.component';
const routes: Routes = [
  //{ path: 'crisis-center', component: CrisisListComponent },
  { path: 'product/:id', component: MyProductAppComponent },
  {
    path: 'home',
    component: MyIndexAppComponent,
    data: { title: 'principal' }
  },
  {
    path: 'car',
    component: MyCarAppComponent,
    data: { title: 'my car' }
  },
  {
    path: 'products/:type',
    component: MyListProductsAppComponent,
    data: { title: 'products' }
  },
  { path: '',
    redirectTo: '/',
    pathMatch: 'full'
  },
  { path: '**', component: MyIndexAppComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
