import { Component, OnInit , Inject,ViewChild} from '@angular/core';
import {InventoryServiceService, Card, ShopCar} from "../inventory-service.service";
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {DialogOverview} from '../dialog/dialog.component';


@Component({
  selector: 'app-my-car-app',
  templateUrl: './my-car-app.component.html',
  styleUrls: ['./my-car-app.component.sass']
})
export class MyCarAppComponent implements OnInit {
	cards: Array<ShopCar>= [];
	number:Array<number>=[1,2,3,4,5]
	subtotal:number = 0;
  COP:string = 'COP'
	constructor(public inventory: InventoryServiceService,public dialog: MatDialog, ) 
	{ 

	}

  	ngOnInit() 
  	{
  		this.cards = this.inventory.mycar;
  		let acum:number = 0;
  		this.cards.forEach(item => {
  			acum += item.product.price * item.cant;
  		})
  		this.subtotal = acum;

  	}

  	clickSelect(){
  		let acum:number = 0;
  		this.cards.forEach(item => {
  			acum += item.product.price * item.cant;
  		})
  		this.subtotal = acum;
  	}

    deleteItems(){

      this.inventory.mycar = [];

    }

    deleteItem(p:ShopCar){

      this.cards = this.cards.filter(e => {
        return e !== p;
      });
      this.inventory.mycar = this.cards;

    }
    
    cancelDeleteElement(dismissMethod: string): void {
      // dismissMethod can be 'cancel', 'overlay', 'close', and 'timer'
      // ... do something
      console.log(dismissMethod)
    }

    openDialog(id:string): void {
      console.log(id)

      let i = <ShopCar>{};
      this.cards.forEach(p => {
        if(p.product.id === id){
          i.product = p.product;
          i.size = p.size;
          i.cant = p.cant;
        }
      })

      const dialogRef = this.dialog.open(DialogOverview, {
        width: '800px',
        data: i
      });

      dialogRef.afterClosed().subscribe(result => {
        console.log('The dialog was closed');
        console.log(result);
        if(result !== null){
          console.log('devuelve result')
          this.cards.forEach(p => {
            console.log(p)
            if(p.product.id === result.product.id){
              console.log(result)
              p.cant = result.cant;
              p.size = result.size;
            }
          })
        }
      });
    }
}




