import { Component , OnInit } from '@angular/core';
import {InventoryServiceService, Card} from "../inventory-service.service";

@Component({
  selector: 'app-my-bestseller-app',
  templateUrl: './my-bestseller-app.component.html',
  styleUrls: ['./my-bestseller-app.component.sass'],
})

export class MyBestsellerAppComponent implements OnInit {
  breakpoint:number = 0
  breakheight:string = ''
  cards: Card[];
  
  constructor(private inventory:InventoryServiceService) { }

  ngOnInit() {
    this.cards = this.inventory.cards.slice(0,6)
    if (window.innerWidth <= 860) { this.breakpoint = 12 } 
    else if(window.innerWidth <= 1300 && window.innerWidth >= 860){ this.breakpoint = 6} 
    else { this.breakpoint = 4}
    if (window.innerWidth <= 860) { this.breakheight = '400px'} 
    else { this.breakheight = '600px'}
  }

  onResize(event) {
    
    if (event.target.innerWidth <= 860) { this.breakpoint = 12 } 
    else if(event.target.innerWidth <= 1300 && event.target.innerWidth >= 860){ this.breakpoint = 6} 
    else { this.breakpoint = 4}
    if (event.target.innerWidth <= 860) { this.breakheight = '400px' } 
    else { this.breakheight = '600px'}
  }

}
