import { Component, OnInit,ViewChild,OnChanges, HostBinding,HostListener,Input } from '@angular/core';
import {MatMenuTrigger} from '@angular/material/menu';
import {InventoryServiceService, Card, ShopCar} from "../inventory-service.service";
import { Observable } from 'rxjs';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';

import {
  trigger,
  state,
  style,
  animate,
  transition,
  // ...
} from '@angular/animations';
@Component({
  selector: 'app-my-toolbar-app',
  templateUrl: './my-toolbar-app.component.html',
  styleUrls: ['./my-toolbar-app.component.sass'],
  animations: [
    trigger('openClose', [
      // ...
      state('noMove', style({
        backgroundColor: 'rgba(0,0,0,0)',
        color: 'white',
      })),
      state('move', style({
        backgroundColor: 'white',
        boxShadow: '0px 0px 5px 0px rgba(0,0,0,1)',
        color: 'black',
      })),
      transition('noMove => move', [
        animate('0.1s')
      ]),
      transition('move => noMove', [
        animate('0.2s')
      ]),
    ]),
    trigger('colorl', [
      // ...
      state('noMove', style({
        color: 'white',
      })),
      state('move', style({
        color: 'black',
      })),
      transition('noMove => move', [
        animate('0.1s')
      ]),
      transition('move => noMove', [
        animate('0.1s')
      ]),
    ]),
  ],

})
export class MyToolbarAppComponent implements OnInit {
	@ViewChild(MatMenuTrigger,{static: true}) trigger: MatMenuTrigger;
	opened: boolean;
	public imgs:Array<string> = new Array<string>();
	public n:number = 0;
	menuExit:boolean = true;
	@Input() isActive:boolean = false;
	@Input() isSticky:boolean = false;
	@Input() nm:boolean = false;
	constructor(public inventory: InventoryServiceService,private route: ActivatedRoute, private router: Router) { }

	ngOnInit() {
		this.imgs.push("assets/img/images.jpg")
	    this.imgs.push("assets/img/tom-model-germaniya-stil.jpg")
	    this.imgs.push("assets/img/women_model_Monika_Jagaciak-24093.jpg")
	    this.imgs.push("assets/img/downloadk.jpg")
	}

  	@HostListener("window:scroll", [])
	onWindowScroll() {
	  window.pageYOffset === 0 && this.isSticky === true ? this.isActive = true : this.isActive = false;
	}

	changeImg(e:number = 0) {
		this.n = e;
	}

	cIcon(){
		if(this.nm){
			if(!this.opened || this.opened === undefined) {
				if(window.pageYOffset === 0){
					this.isActive = false
				}
			}
			else
			{ 
				if(window.pageYOffset === 0){
					this.isActive = true				}
			}
		}
	}
	mouseOver() {
		if(!this.trigger.menuOpen){
			this.isActive = false;
			this.trigger.openMenu();
		}
	}

	menuOver() {		
		this.menuExit = false;
	}

	menuLeave() {
		this.menuExit = true;
		if(window.pageYOffset === 0 && this.nm)
			this.isActive = true;
		this.mouseLeave()
	}

	hide(e){
		this.trigger.restoreFocus = false;
	}

	mouseLeave() {
		setTimeout(() => {
			if(this.menuExit){
				this.trigger.closeMenu();
				if(window.pageYOffset === 0 && this.nm)
					this.isActive = true;
			}
		}, 200)
	}

}
