import { Component, OnInit } from '@angular/core';
import { MatCarousel, MatCarouselComponent } from '@ngmodule/material-carousel';
@Component({
  selector: 'app-my-carousel-app',
  templateUrl: './my-carousel-app.component.html',
  styleUrls: ['./my-carousel-app.component.sass']
})
export class MyCarouselAppComponent implements OnInit {

  public slidesList = new Array<string>();
  public showContent = false;

  public timings = '250ms ease-in';
  public autoplay = true;
  public interval = 5000;
  public loop = true;
  public hideArrows = true;
  public hideIndicators = false;
  public color = '00000040';
  public maxWidth = 'auto';
  public proportion = 25;
  public slides = this.slidesList.length;
  public overlayColor = '#00000040';
  public hideOverlay = false;
  public useKeyboard = true;
  public useMouseWheel = false;
  public orientation = 'ltr';
  public log: string[] = [];

  ngOnInit() {
    this.slidesList.push("assets/img/women_model_Monika_Jagaciak-24093.jpg")
    this.slidesList.push("assets/img/tom-model-germaniya-stil.jpg")
    this.slidesList.push("assets/img/downloadk.jpg")
    if (window.innerWidth <= 960) { this.hideIndicators = true } else { this.hideIndicators = false }
  }

  public onChange(index: number) {
    this.log.push(`MatCarousel#change emitted with index ${index}`);
  }

  onResize(event) {
    
    if (event.target.innerWidth <= 960) { this.hideIndicators = true } else { this.hideIndicators = false }
  }

}
