import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-my-footer-app',
  templateUrl: './my-footer-app.component.html',
  styleUrls: ['./my-footer-app.component.sass']
})
export class MyFooterAppComponent implements OnInit {
  breakpoint:number = 0
  constructor() { }

  ngOnInit() {
    if (window.innerWidth <= 860) { this.breakpoint = 12 }
    else { this.breakpoint = 4}
  }

  onResize(event) {
    if (event.target.innerWidth <= 860) { this.breakpoint = 12 } 
    else { this.breakpoint = 4}
  }

 

}
