import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { switchMap } from 'rxjs/operators';
import {InventoryServiceService, Card} from "../inventory-service.service";
import { FormBuilder, FormGroup, FormArray, FormControl, ValidatorFn } from '@angular/forms';
@Component({
  selector: 'app-my-product-app',
  templateUrl: './my-product-app.component.html',
  styleUrls: ['./my-product-app.component.sass']
})
export class MyProductAppComponent implements OnInit {
	card: Card;
	number:Array<number>=[1,2,3,4,5]
	sizeT:Array<any>=[{use: false, s: 'xs'},{use: false, s: 's'},{use: false, s: 'm'},{use: false, s: 'l'},{use: false, s: 'xl'}];
	sizeV:string = '';
	form: FormGroup;
	features:string[]= ['Cras dapibus. Vivamus elementum semper nisi','Aenean vulputate eleifend tellus. Aenean leo ligula, porttitor eu, consequat vitae, eleifend ac','enim. Aliquam lorem ante, dapibus in','viverra quis, feugiat a, tellus. Phasellus viverra nulla ut metus varius laoreet'];
	materials:string[]= ['Maecenas tempus','tellus eget condimentum rhoncus']
	COP:string = 'COP'
	constructor(private route: ActivatedRoute, private router: Router, private inventory: InventoryServiceService, private formBuilder: FormBuilder) {}


	ngOnInit() {
		console.log(this.features);
	  let id = this.route.snapshot.paramMap.get('id');
  	  this.card = this.inventory.getCard(id);
  	  this.form = this.formBuilder.group({
	      cant: []
	    });
	}

	size(t:string){
		this.sizeV = t
		this.sizeT.forEach(s => {
			if(s.s === t){
				s.use = true;
			}else{
				s.use = false;
			}
		});
	}

	shop(){
		let obj:any = {'product': this.card,'cant': this.form.value.cant ,'size': this.sizeV};
		this.inventory.addToCar(obj);
	}

}
