import { Component, Inject, OnInit } from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {ShopCar} from "../inventory-service.service";

@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: './dialog.component.html',
  styleUrls: ['./dialog.component.sass']
})
export class DialogOverview implements OnInit {
	number:Array<number>=[1,2,3,4,5]
	sizeV:string = '';
	cant:number = 0;
	sizeT:Array<any>=[{use: false, s: 'xs'},{use: false, s: 's'},{use: false, s: 'm'},{use: false, s: 'l'},{use: false, s: 'xl'}];
	COP:string = 'COP'
  	constructor(
	    public dialogRef: MatDialogRef<DialogOverview>,
	    @Inject(MAT_DIALOG_DATA) public data: ShopCar
    ) {}
  	
  	ngOnInit() {}

	size(t:string){
		this.sizeV = t;
		this.data.size = t;
		this.sizeT.forEach(s => { s.s === t ? s.use = true : s.use = false }); 
	}

	clickSelect(n:number){
		console.log(this.data)
  	}

	onNoClick(): void {
		this.data = null
		this.dialogRef.close();
	}

}
