import { Component, OnInit, ViewChild } from '@angular/core';
import {PageEvent, MatPaginator} from '@angular/material/paginator';
import {FormControl} from '@angular/forms' ;
import {debounceTime} from 'rxjs/operators' ;
import {Sort} from '@angular/material/sort';
import {InventoryServiceService, Card} from "../inventory-service.service";
import { Router, ActivatedRoute, ParamMap } from '@angular/router';

@Component({
  selector: 'app-my-list-products-app',
  templateUrl: './my-list-products-app.component.html',
  styleUrls: ['./my-list-products-app.component.sass']
})
export class MyListProductsAppComponent implements OnInit {
	breakpoint:number = 0;
	breakheight:string = '';
	page_size:number = 5;
	page_number:number = 1;
	cards:Card[];
	sortedData: Card[];
	search = new FormControl('');
	search_use:string = '';
	type_select:string = ''
	cardByType:Card[];
	cardBySearch:Card[];
	COP:string = 'COP'
	@ViewChild(MatPaginator,{static: true}) paginator: MatPaginator;
	constructor(private inventory:InventoryServiceService,private router: Router,private route: ActivatedRoute) { 
		this.cards = this.inventory.cards
		this.sortedData = this.cards.slice();
	}

	ngOnInit() {
		
		if (window.innerWidth <= 1600) { this.breakpoint = 12 } 
	    else { this.breakpoint = 6}
	    //this.breakheight = '600px'
	    if (window.innerWidth <= 860) { this.breakheight = '700px' } 
    	else { this.breakheight = '600px'}
		this.route.params.subscribe(params => {
			this.type_select = this.route.snapshot.paramMap.get('type');
			if(this.type_select === 'all'){
				this.cardByType = this.cards;
			}else{
				this.cardByType = this.cards.filter(card => card.type === this.type_select);
			}
	    });
		this.search.valueChanges
		.pipe(
			debounceTime(300)
		)
		.subscribe((value) => {
			this.search_use = value;
			this.cardByType = this.cards.filter(card => card.name.toUpperCase().includes(this.search_use.toUpperCase()));
		});
		this.paginator._intl.itemsPerPageLabel = 'productos por pagina';
		this.paginator._intl.previousPageLabel = 'pagina previa';
		this.paginator._intl.nextPageLabel = 'proxima pagina';
		this.paginator._intl.getRangeLabel = (page, size, length) => `Pagina ${page+1} de ${Math.ceil(length / size)}`;
	}

	onResize(event) {
		
		if (event.target.innerWidth <= 1600) { this.breakpoint = 12 } 
	    else { this.breakpoint = 6}
	    if (event.target.innerWidth <= 860) { this.breakheight = '700px' } 
    	else { this.breakheight = '600px'}
	}

	handlePage(e: PageEvent){
		this.page_size = e.pageSize;
		this.page_number = e.pageIndex + 1
	}

  	sortData(sort: Sort) {
	    const data = this.cards.slice();
	    if (!sort.active || sort.direction === '') {
	      this.sortedData = data;
	      return;
	    }

	    this.sortedData = data.sort((a, b) => {
	      const isAsc = sort.direction === 'asc';
	      switch (sort.active) {
	        case 'name': return this.compare(a.name, b.name, isAsc);
	        case 'price': return this.compare(a.price, b.price, isAsc);
	        default: return 0;
	      }
	    });
  	}


	compare(a: number | string, b: number | string, isAsc: boolean) {
	  return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
	}


}
